import axios from 'axios'

const local = 'http://192.168.0.22:8088'
// const server = 'http://ec2-3-36-180-201.ap-northeast-2.compute.amazonaws.com:8088'




// 컨텐츠 포탈 호출
function contsload(){
    return axios.get(`${local}/contents/list`)
    .then(({data}) => {
        // console.log(data);
        return data
    })
    .catch((err) => {
        console.log(err);
    })
}

